import React, { Component } from 'react'
import './App.css'
import MicroApp from '@workgrid/micro-app'

class App extends Component {
  constructor() {
    super()
    // Create a new instance of a MicroApp
    this.microapp = new MicroApp({
      id: 'slow-to-load-microapp',
      audience: 'slow-to-load'
    })

    this.state = {
      feedback: ''
    }

    this.showDetail = this.showDetail.bind(this)
    this.hideDetail = this.hideDetail.bind(this)
    this.getToken = this.getToken.bind(this)
  }

  componentDidMount() {
    // As soon as possible and once, tell Workgrid that you are ready
    // I put it in the did mount since that means we are ready to roll
    setTimeout(() => {
      this.microapp.initialize()

      this.setState({
        feedback: 'Initialized'
      })
    }, 10000)
  }

  showDetail() {
    // Show detail by calling the .showDetail() on the microapp
    // The URL should be another microapp ready webpage, showing
    // standard webpages is not supported at this time

    // In this example I'm just showing summary again
    this.microapp.showDetail({ url: `${window.location.origin}${window.location.pathname}`, title: 'Slow Detail' })

    this.setState({
      feedback: 'Show Detail Sent'
    })
  }

  hideDetail() {
    this.microapp.hideDetail()
    this.setState({
      feedback: 'Detail hidden'
    })
  }

  async getToken() {
    this.setState({
      feedback: 'Asking for Token...'
    })
    const token = await this.microapp.getToken()
    this.setState({
      feedback: `Got Token ${token.length}`
    })
  }

  render() {
    const { feedback } = this.state
    return (
      <div className="App">
        <span role="img" aria-label="turtle" style={{ fontSize: '5em' }}>
          üê¢
        </span>
        <pre>{feedback}</pre>
        <div className="ActionBar">
          <button onClick={this.showDetail}>Show Detail</button>
          <button onClick={this.hideDetail}>Hide Detail</button>
          <button onClick={this.getToken}>Get Token</button>
        </div>
      </div>
    )
  }
}

export default App
